<template>
  <div class="content">
    <!--   -->
    <h1 class="text-2xl font-bold" style="margin: 15px -25px -35px 25px;"></h1>
    <!--    -->
    <div class="flex mx-1 my-20 h-full">
      <div class="w-4/5">
        <div class="flex flex-row-reverse">
        </div>
        <ag-grid-vue class="ag-theme-alpine" style="width: 100%; height: 550px;" :columnDefs="colDefs"
          :rowData="rowData.value" rowSelection="multiple" suppressRowClickSelection="true" :domLayout="'autoHeight'"
          :defaultColDef="defaultColDef" @grid-ready="onGridReady" @selection-changed="onSelectionChanged">
        </ag-grid-vue>
      </div>
      <!--   -->
      <div class="ml-4 border border-gray-400 p-4 rounded" style="width: 15%; height: 50%;">
        <h1 class="mt-4 font-black mb-7 text-xl"> </h1>
        <div class="flex justify-between mb-4">
          <h2 class="pr-10">  </h2>
          <h2 class="font-bold">{{ totalItemAmount }}</h2>
        </div>
        <div class="flex justify-between mb-4">
          <h2 class="pr-10"> </h2>
          <h2 class="font-bold">{{ totalQuantity }}</h2>
        </div>
        <div class="flex justify-between mb-4">
          <h2 class="pr-10">+</h2>
          <h2 class="font-bold">{{ shippingFee }}</h2>
        </div>
        <hr class="my-3">
        <div class="flex justify-between">
          <h2></h2>
          <h2 class="font-bold">{{ totalAmount }}</h2>
        </div>
        <!--     -->
        <div class="mt-10">
          <h2 class="font-bold">  </h2>
          <div>
            <label for="recipient"> </label>
            <input type="text" id="recipient" v-model="recipient">
          </div>
          <div>
            <label for="postcode"></label>
            <input type="text" id="postcode" v-model="postcode">
            <button @click="findAddress"></button>
          </div>
          <div>
            <label for="address"> </label>
            <input type="text" id="address" v-model="address">
          </div>
          <div>
            <label for="detailAddress">   </label>
            <input type="text" id="detailAddress" v-model="detailAddress">
          </div>
          <div>
            <label for="phoneNumber"> </label>
            <input type="text" id="phoneNumber" v-model="phoneNumber">
          </div>
          <div>
            <label for="deliveryRequest"> </label>
            <select id="deliveryRequest" v-model="deliveryRequest">
              <option value=""></option>
              <option value="  ">  </option>
              <option value=" "> </option>
              <option value="  ">  </option>
            </select>
          </div>
        </div>
        <!--   -->
        <button class="bg-gray-800 text-white rounded-md hover:bg-blue-600 mt-10 p-1 w-full h-14"
          @click="placeOrder"></button>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, ref } from 'vue'
import 'ag-grid-community/styles/ag-grid.css'
import 'ag-grid-community/styles/ag-theme-quartz.css'
import { AgGridVue } from 'ag-grid-vue3'
import axios from 'axios'

export default {
  name: 'ContractHomeView',
  components: {
    AgGridVue
  },
  setup () {
    const rowData = reactive([])
    const totalItemAmount = ref(0) //   
    const totalAmount = ref(0) //   (  + )
    const totalQuantity = ref(0)
    const shippingFee = ref(0) // 
    const gridApi = ref()

    const colDefs = reactive([
      {
        headerName: '',
        field: 'ord_chk',
        //      
        checkboxSelection: true,
        //     /  
        headerCheckboxSelection: true,
        suppressSizeToFit: true,
        maxWidth: 60
      },
      {
        headerName: '',
        field: 'img_itm',
        width: 80,
        cellRenderer: function (params) {
          const imagePath = `/img/itme/${params.value}`
          return `<img src="${imagePath}" style="width: 75%; height: 100%;">`
        }
      },
      { headerName: '', field: 'ods_cod', hide: true, cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 50 },
      { headerName: '', field: 'ods_typ', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 80, maxWidth: 80, sortable: true, hide: true },
      { headerName: '', field: 'ods_gub', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 120, maxWidth: 120, sortable: true, hide: true },
      { headerName: '', field: 'ods_nam', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 200, maxWidth: 200, sortable: true },
      { headerName: '', field: 'gub_spc', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 80, maxWidth: 80, sortable: true },
      { headerName: '', field: 'gub_unt', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 80, maxWidth: 80, sortable: true },
      { headerName: '', field: 'gub_box', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 130, maxWidth: 130, sortable: true },
      { headerName: '', field: 'ods_prc', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 100, maxWidth: 100, sortable: true },
      { headerName: '', field: 'ods_qty', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 100, maxWidth: 100, sortable: true },
      { headerName: '', field: 'ods_tot', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 100, maxWidth: 100, sortable: true },
      { headerName: '', field: 'ods_oid', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 140, maxWidth: 140, sortable: true, hide: true },
      { headerName: '', field: 'ods_dat', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 100, maxWidth: 100, sortable: true, hide: true },
      { headerName: 'Seq', field: 'seq', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 90, maxWidth: 90, sortable: true, hide: true }
    ])
    //     .
    const loadData = async () => {
      try {
        const response = await axios.get('/api/scmusr/order')
        rowData.value = response.data
      } catch (error) {
        console.error('   :', error)
      }
    }
    loadData()

    //  
    const removeSelectedItems = () => {
      const selectedData = gridApi.value.getSelectedRows()

      //    
      if (selectedData.length === 0) {
        alert('  .')
        return //    
      }

      //    
      const isConfirmed = confirm('  ?')

      //  ''    
      if (isConfirmed) {
        const selectedSeqs = selectedData.map(row => row.seq)

        axios.delete('/api/scmusr/deleteOrder', { data: selectedSeqs })
          .then(() => {
            alert('  .')
            loadData() //    
          })
          .catch(error => console.error('   :', error))
      }
    }

    // 
    const onSelectionChanged = () => {
      if (gridApi.value) {
        const selectedData = gridApi.value.getSelectedRows()

        //     (ods_tot )
        totalItemAmount.value = selectedData.reduce((acc, row) => acc + parseInt(row.ods_tot, 10), 0)

        //  
        totalQuantity.value = selectedData.reduce((acc, row) => acc + parseInt(row.ods_qty, 10), 0)

        //  :    20,000   3,000,   0
        shippingFee.value = totalItemAmount.value < 20000 ? 3000 : 0

        //    (  + )
        totalAmount.value = totalItemAmount.value + shippingFee.value
      } else {
        console.error('GridApi   .')
      }
    }
    const onGridReady = (params) => {
      gridApi.value = params.api
    }

    return {
      rowData,
      colDefs,
      onSelectionChanged,
      onGridReady,
      totalAmount,
      totalQuantity,
      totalItemAmount,
      shippingFee,
      removeSelectedItems
    }
  }
}
</script>

<style scoped>
</style>
