<template>
  <div class="content">
    <ModalApp v-if="showDeliveryModal" :visible="showDeliveryModal" :addresses="deliveryAddresses"
      @close="showDeliveryModal = false" @select="selectAddress" @add-address="handleAddAddress"
      @updated="fetchDeliveryAddresses" @deleted="fetchDeliveryAddresses" />
    <h1 class="text-2xl font-bold" style="margin: 15px -25px -35px 25px;"><i class="fa-solid fa-cart-shopping"></i> </h1>
    <div class="flex mx-4 my-10 h-full">
      <div class="w-4/5">
        <div class="flex flex-row-reverse">
          <button class="bg-red-500 text-white rounded-md hover:bg-red-300 mb-2 p-2 h-10"
            @click="removeSelectedItems"> </button>
        </div>
        <ag-grid-vue class="ag-theme-alpine" style="width: 100%; height: 650px;" :columnDefs="colDefs"
          :rowData="rowData.value" rowSelection="multiple" suppressRowClickSelection="true"
          alwaysShowVerticalScroll="true" :defaultColDef="defaultColDef" @grid-ready="onGridReady"
          @selection-changed="onSelectionChanged" @cell-value-changed="onCellValueChanged">
        </ag-grid-vue>
      </div>

      <div class="ml-4 border border-gray-300 p-4 rounded shadow-sm"
        style="width: 20%; max-height: 700px; overflow-y: auto;">
        <div class="flex justify-between font-semibold text-xl mb-4"> : <span class="text-black">{{ totalAmountWithTax }}</span></div>
        <div class="space-y-4">
          <div class="flex justify-between">
            <h2>  </h2>
            <p class="font-semibold">{{ totalItemAmount }}</p>
          </div>
          <div class="flex justify-between">
            <h2> </h2>
            <p class="font-semibold">{{ totalQuantity }}</p>
          </div>
          <div class="flex justify-between">
            <h2></h2>
            <p class="font-semibold">{{ shippingFee }}</p>
          </div>
          <div class="flex justify-between">
            <h2> (10%)</h2>
            <p class="font-semibold">{{ vatAmount }}</p>
          </div>
          <hr class="my-4">
          <div>
            <div class=""><h2 class="font-semibold text-lg mb-3">  </h2>
            <button
              class="bg-blue-500 hover:bg-blue-600 text-white font-medium rounded px-4 py-2 transition-colors mb-4"
              @click="fetchDeliveryAddresses"> </button></div>
            <div>
              <input type="radio" id="defaultAddress" value="default" v-model="deliveryAddressType" class="mr-2"
                @change="onAddressTypeChange">
              <label for="defaultAddress" class="text-sm font-medium"> </label>
            </div>
            <div v-if="selectedAddress" class="mt-4 space-y-2">
              <div class="flex justify-between">
                <span class="font-semibold text-gray-700"> :</span>
                <span class="text-gray-900">{{ selectedAddress.dv_nam }}</span>
              </div>
              <div class="flex justify-between">
                <span class="font-semibold text-gray-700">:</span>
                <span class="text-gray-900">{{ selectedAddress.dv_adr }}</span>
              </div>
              <div class="flex justify-between">
                <span class="font-semibold text-gray-700"> :</span>
                <span class="text-gray-900">{{ selectedAddress.dv_dti }}</span>
              </div>
              <div class="flex justify-between">
                <span class="font-semibold text-gray-700">:</span>
                <span class="text-gray-900">{{ selectedAddress.dv_phn }}</span>
              </div>
              <div class="mt-4">
                <label for="deliveryRequestDate" class="block text-gray-700 font-medium"></label>
                <input type="date" id="deliveryRequestDate" v-model="deliveryRequestDate"
                  class="mt-1 block w-full px-3 py-2 bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm">
              </div>
              <div class="mt-4">
                <label for="deliveryRequest" class="block text-gray-700 font-medium"> </label>
                <select id="deliveryRequest" v-model="deliveryRequest" @change="checkCustomRequest"
                  class="mt-1 block w-full pl-3 pr-10 py-2 text-base border-gray-300 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm rounded-md">
                  <option value=""></option>
                  <option value="  ">  </option>
                  <option value=" "> </option>
                  <option value=" "> </option>
                  <option value="  ">  </option>
                  <option value=" "> </option>
                </select>
                <input v-if="deliveryRequest === ' '" type="text" v-model="customRequest"
                  class="mt-1 block w-full px-3 py-2 bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                  placeholder="  ">
              </div>
            </div>

            <button
              class="mt-4 w-full bg-gray-800 hover:bg-blue-600 text-white font-medium rounded px-4 py-2 transition-colors"
              @click="orderSelectedItems"></button>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import { reactive, ref, onMounted } from 'vue'
import 'ag-grid-community/styles/ag-grid.css'
import 'ag-grid-community/styles/ag-theme-quartz.css'
import { AgGridVue } from 'ag-grid-vue3'
import axios from 'axios'
import router from '@/router'
import store from '@/store'
import ModalApp from '@/components/modal/ModalApp.vue'

export default {
  name: 'ContractHomeView',
  components: {
    AgGridVue,
    ModalApp
  },
  setup () {
    const rowData = reactive([])
    const totalItemAmount = ref(0) //   
    const totalAmount = ref(0) //   (  + )
    const vatAmount = ref(0) // 
    const totalAmountWithTax = ref(0) //   ( )
    const totalQuantity = ref(0)
    const shippingFee = ref(0) // 
    const gridApi = ref()
    const deliveryAddresses = ref([]) //   
    const selectedAddress = ref(null) //   
    const deliveryAddressType = ref('default') //   
    const showDeliveryModal = ref(false) //    
    const usrId = store.state.usrInfo.usrId
    const deliveryRequest = ref('')
    const isCustomRequest = ref(false)
    const customRequest = ref('')
    const deliveryRequestDate = ref('') //    ref
    const editingAddress = ref(null) //    
    const addresses = ref([]) //     -
    const currentView = ref('list')

    const colDefs = reactive([
      {
        headerName: '',
        field: 'ord_chk',
        checkboxSelection: true,
        headerCheckboxSelection: true,
        suppressSizeToFit: true,
        maxWidth: 60
      },
      {
        headerName: '',
        field: 'img_itm',
        width: 80,
        cellRenderer: function (params) {
          const imagePath = `/img/item/${params.value}`
          return `<img src="${imagePath}" style="width: 75%; height: 100%;">`
        }
      },
      { headerName: '', field: 'ods_cod', hide: true, cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 50 },
      { headerName: '', field: 'ods_typ', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 80, maxWidth: 80, sortable: true, hide: true },
      { headerName: '', field: 'ods_gub', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 120, maxWidth: 120, sortable: true, hide: true },
      { headerName: '', field: 'ods_nam', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 200, maxWidth: 200, sortable: true },
      { headerName: '', field: 'gub_spc', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 80, maxWidth: 80, sortable: true, cellStyle: { 'text-align': 'right' } },
      { headerName: '', field: 'gub_unt', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 80, maxWidth: 80, sortable: true, cellStyle: { 'text-align': 'center' } },
      { headerName: '', field: 'gub_box', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 130, maxWidth: 130, sortable: true, cellStyle: { 'text-align': 'right' } },
      { headerName: '', field: 'ods_prc', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 100, maxWidth: 100, sortable: true, valueFormatter: params => numberWithCommas(params.value), cellStyle: { 'text-align': 'right' } },
      { headerName: '', field: 'ods_qty', editable: true, cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 100, maxWidth: 100, sortable: true, cellStyle: { 'text-align': 'right' } },
      { headerName: '', field: 'ods_tot', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 100, maxWidth: 100, sortable: true, valueFormatter: params => numberWithCommas(params.value), cellStyle: { 'text-align': 'right' } },
      { headerName: '', field: 'gub_mic', cellClass: 'textRightCell', minWidth: 90, maxWidth: 90, cellStyle: params => Object.assign({ 'text-align': 'right' }, params.value === 0 ? { color: 'red', fontWeight: 'bold' } : {}) },
      {
        headerName: ' ',
        field: 'stock_status',
        cellStyle: params => Object.assign({ 'text-align': 'center' }, params.value === ' ' ? { color: 'red', fontWeight: 'bold' } : {})
      },
      { headerName: '', field: 'ods_oid', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 140, maxWidth: 140, sortable: true, hide: true },
      { headerName: '', field: 'ods_dat', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 100, maxWidth: 100, sortable: true, hide: true },
      { headerName: 'Seq', field: 'seq', cellClass: 'textLeftCell', suppressSizeToFit: true, minWidth: 90, maxWidth: 90, sortable: true, hide: true }
    ])

    const loadData = async () => {
      try {
        const response = await axios.get('/api/scmusr/order', { params: { usr_id: store.state.usrInfo.usrId } })
        rowData.value = response.data.map(item => ({
          ...item,
          stock_status: item.gub_mic > 0 ? ' ' : ' '
        }))
      } catch (error) {
        console.error('   :', error)
      }
    }
    loadData()

    const removeSelectedItems = () => {
      const selectedData = gridApi.value.getSelectedRows()

      if (selectedData.length === 0) {
        alert('  .')
        return
      }

      const isConfirmed = confirm('  ?')

      if (isConfirmed) {
        const selectedSeqs = selectedData.map(row => row.seq)

        axios.delete('/api/scmusr/deleteOrder', { data: selectedSeqs })
          .then(() => {
            alert('  .')
            loadData()
          })
          .catch(error => console.error('   :', error))
      }
    }

    const onSelectionChanged = () => {
      if (gridApi.value) {
        const selectedData = gridApi.value.getSelectedRows()

        const totalItemAmountNumber = selectedData.reduce((acc, row) => acc + parseInt(row.ods_tot, 10), 0)
        totalItemAmount.value = totalItemAmountNumber.toLocaleString()

        const totalQuantityNumber = selectedData.reduce((acc, row) => acc + parseInt(row.ods_qty, 10), 0)
        totalQuantity.value = totalQuantityNumber.toLocaleString()

        const shippingFeeNumber = selectedData.length === 0 ? 0 : (totalItemAmountNumber < 20000 ? 3000 : 0)
        shippingFee.value = shippingFeeNumber.toLocaleString()

        const totalAmountNumber = totalItemAmountNumber + shippingFeeNumber
        totalAmount.value = totalAmountNumber.toLocaleString()

        const vatAmountNumber = Math.round(totalAmountNumber * 0.1)
        vatAmount.value = vatAmountNumber.toLocaleString()

        const totalAmountWithTaxNumber = totalAmountNumber + vatAmountNumber
        totalAmountWithTax.value = totalAmountWithTaxNumber.toLocaleString()
      } else {
        console.error('GridApi   .')
      }
    }

    const onCellValueChanged = async (event) => {
      const { data, colDef, newValue } = event
      if (colDef.field === 'ods_qty') {
        const maxQty = data.gub_mic
        if (newValue > maxQty) {
          data.ods_qty = maxQty
          alert(`    ${maxQty}.`)
        } else {
          data.ods_qty = newValue
          alert(' .')
        }
        data.ods_tot = data.ods_prc * data.ods_qty

        try {
          await axios.post('/api/scmusr/updateOrderQuantity', { ods_cod: data.ods_cod, new_quantity: data.ods_qty })
        } catch (error) {
          console.error('    :', error)
          alert('    .')
        }

        gridApi.value.refreshCells({ rowNodes: [event.node], force: true })
      }
    }

    const checkCustomRequest = () => {
      isCustomRequest.value = deliveryRequest.value === ' '
    }
    // 
    const orderSelectedItems = async () => {
      const selectedData = gridApi.value.getSelectedRows()

      if (selectedData.length === 0) {
        alert('  .')
        return
      }

      //   
      const outOfStockItems = selectedData.filter(item => item.stock_status === ' ')
      if (outOfStockItems.length > 0) {
        alert('   .   .')
        return
      }

      const selectedProducts = selectedData.map(product => ({
        odr_id: store.state.usrInfo.usrId,
        ods_cod: product.ods_cod,
        ods_gub: product.ods_gub,
        ord_nam: store.state.usrInfo.usrCom,
        odr_com: selectedAddress.value.dv_nam,
        odr_tel: selectedAddress.value.dv_phn,
        odr_als: selectedAddress.value.dv_als,
        odr_del: selectedAddress.value.dv_adr,
        odr_dti: selectedAddress.value.dv_dti,
        odr_drd: deliveryRequestDate.value,
        odr_txt: deliveryRequest.value === ' ' ? customRequest.value : deliveryRequest.value,
        ods_qty: product.ods_qty
      }))

      try {
        await axios.post('/api/scmusr/completeOrder', selectedProducts)
        console.log(selectedProducts)
        const confirmResult = window.confirm(' .   ?')
        if (confirmResult) {
          router.push('/contract/created')
        }
        loadData()
      } catch (error) {
        console.error('    :', error)
        alert('    .')
      }
    }

    const selectAddress = (address) => {
      selectedAddress.value = address
      showDeliveryModal.value = false
      deliveryAddressType.value = ''
    }

    const fetchDeliveryAddresses = async () => {
      try {
        const response = await axios.get(`/api/delivery-addresses/${usrId}`)
        deliveryAddresses.value = response.data
        showDeliveryModal.value = true
      } catch (error) {
        console.error('     :', error)
      }
    }

    const fetchDefaultDeliveryAddress = async () => {
      try {
        const response = await axios.get(`/api/delivery-addresses/${usrId}/default`)
        selectedAddress.value = response.data
        deliveryAddressType.value = 'default'
        showDeliveryModal.value = false
      } catch (error) {
        console.error('      :', error)
      }
    }

    onMounted(() => {
      loadData()
      fetchDefaultDeliveryAddress()
    })

    const onAddressTypeChange = async (event) => {
      if (event.target.value === 'default') {
        await fetchDefaultDeliveryAddress()
      } else {
        selectedAddress.value = null
      }
    }

    const handleAddAddress = (newAddress) => {
      deliveryAddresses.value.push(newAddress)
      fetchDeliveryAddresses()
    }

    const handleDeleted = async () => {
      await fetchDeliveryAddresses()
    }

    const fetchAddresses = async () => {
      try {
        const response = await axios.get(`/api/delivery-addresses/${usrId}`)
        addresses.value = response.data
        currentView.value = 'list'
      } catch (error) {
        console.error('    :', error)
      }
    }

    const deleteAddress = async (dvNum) => {
      if (!confirm('  ?')) return
      try {
        await axios.delete(`/api/delivery-addresses/delete/${dvNum}`)
        alert(' .')
        await fetchDeliveryAddresses()
        currentView.value = 'list'
      } catch (error) {
        console.error('    :', error)
        alert('    .')
      }
    }

    const submitEditAddress = async () => {
      editingAddress.value.dv_mus = editingAddress.value.dv_mus ? 'Y' : 'N'
      try {
        await axios.put(`/api/delivery-addresses/update/${editingAddress.value.dv_num}`, editingAddress.value)
        alert('  .')
        fetchDeliveryAddresses()
        editingAddress.value = null
        currentView.value = 'list'
      } catch (error) {
        console.error('    :', error)
        alert('    .')
      }
    }

    const editAddress = (address) => {
      editingAddress.value = { ...address }
      currentView.value = 'update'
    }

    const cancelEdit = () => {
      editingAddress.value = null
      currentView.value = 'list'
    }

    const onGridReady = (params) => {
      gridApi.value = params.api
    }

    function numberWithCommas (value) {
      return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
    }

    return {
      rowData,
      colDefs,
      onSelectionChanged,
      onGridReady,
      onCellValueChanged,
      totalAmount,
      totalQuantity,
      totalItemAmount,
      shippingFee,
      vatAmount,
      totalAmountWithTax,
      removeSelectedItems,
      orderSelectedItems,
      deliveryAddresses,
      selectedAddress,
      deliveryAddressType,
      onAddressTypeChange,
      fetchDeliveryAddresses,
      selectAddress,
      showDeliveryModal,
      fetchDefaultDeliveryAddress,
      deliveryRequest,
      isCustomRequest,
      customRequest,
      deliveryRequestDate,
      handleAddAddress,
      editingAddress,
      submitEditAddress,
      deleteAddress,
      editAddress,
      cancelEdit,
      fetchAddresses,
      addresses,
      handleDeleted,
      checkCustomRequest
    }
  }
}
</script>

<style scoped>
.form-group {
  margin-bottom: 1rem;
}

.form-control {
  width: 100%;
  padding: 0.2rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
}

select.form-control {
  appearance: none;
}

button {
  cursor: pointer;
}
</style>
